.footer {
    width: calc(100% - 305px);
    height: 109px;
    background: #1F1F29;
    margin-left: 305px;
    position: absolute;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
}
.logo__wrapper, .steam__wrapper {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    color: #FFFFFF;
    margin-left: 24px;
}
.steam__wrapper {
    margin-right: 115px;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 21px;
    text-align: right;
    color: #595D6E;
}
.steam__wrapper img {
    width: 195px;
    margin-bottom: 8px;
}
.menu {
    height: 100%;
    display: flex;
    justify-content: flex-start;
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    color: #595D6E;
    align-items: center;
    text-transform: uppercase;
}
.menu span {
    margin: 0 20px;
    cursor: pointer ;
    transition: .3s all;
}
.menu span:hover {
    color: #FFF;
    transition: .3s all;
}
.logo__wrapper img {
    width: 115px;
    margin-bottom: 8px;
}