.slider__wrapper {
    width: 100%;
    height: 250px;
    background: linear-gradient(90deg, rgba(75, 83, 183, 0.3) 0%, rgba(141, 121, 198, 0.3) 100%), linear-gradient(90deg, rgba(34, 34, 45, 0.5) 0%, rgba(34, 34, 45, 0) 100%);
    position: relative;
}
.slider__img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: .3s all;
}
.button {
    padding: 12px 24px;
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    color: #2F2F3F;
    text-transform: uppercase;
    position: absolute;
    z-index: 2;
    left: 28px;
    bottom: 30px;
    background: #FCB90B;
    box-shadow: 0px 4px 16px rgba(34, 34, 45, 0.8);
    border: 2px solid #FCB90B;
    border-radius: 4px;
    cursor: pointer;
    transition: .2s all;
}
.button:hover {
    background: #1F1F29;
    border: 2px solid #FCB90B;
    color: #FCB90B;
    transition: .2s all;
}
.block__circle {
    position: absolute;
    bottom: 24px;
    left: 50%;
    transform: translate(-50%);

    display: flex;
    z-index: 10;
}
.circle {
    cursor: pointer;
    background: #FFF;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin-right: 10px;
    transition: .3s all;
}