.sidebar__wrapper {
    background: #252632;
    box-shadow: 0px 4px 16px rgba(34, 34, 45, 0.8);
    border-radius: 5px;
    width: 235px;
}
.block__1 {
    padding: 32px 24px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    flex-direction: column;
    line-height: 14px;
    color: #595D6E;
    text-transform: uppercase;
    border-bottom: 1px solid rgba(87, 93, 111, 0.2);
}
.block__title {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 14px;
    color: #595D6E;
    margin-bottom: 32px;
}
.current__matches, .close__matches, .completed__matches  {
    width: 100%;
    height: 61px;
    background: #2F2F3F;
    box-shadow: 0px 4px 16px rgba(34, 34, 45, 0.8);
    border-radius: 5px;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.close__matches {
    margin-top: 12px;
    justify-content: space-between;
    padding: 0px 16px;
    width: calc(100% - 32px);
}
.completed__matches {
    margin-top: 12px;
    justify-content: space-between;
    padding: 0 24px;
    width: calc(100% - 48px);
    position: relative;
}
.completed__left {
    position: absolute;
    top: 0;
    left: 0;
}
.completed__right {
    position: absolute;
    top: 0;
    right: 0;
}
.close__middle {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.close__time {
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 28px;
    color: #FFFFFF;
}
.close__bottom {
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 14px;
    color: #575D6F;
}
.live__button {
    border: 1px solid #399241;
    box-sizing: border-box;
    border-radius: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-style: normal;
    font-weight: bold;
    font-size: 12px;
    line-height: 21px;
    color: #399241;
    width: 60px;
}
.live__button > :last-child {
    margin-left: 4px;
}
