.match__wrapper {
    margin-top: 30px;
    width: 100%;
    background: #252632;
    min-height: 100px;
}
.match__block__title {
    height: 50px;
    width: calc(100% - 32px);
    background: #1F1F29;
    border-radius: 3px 3px 0px 0px;
    padding: 0px 16px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 4px;
}
.title__value {
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    color: #FFFFFF;
    text-transform: uppercase;
    margin-left: 13px;
}
.title__left {
    display: flex;
    align-items: center;
}
.title__input {
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: #595D6E;
    outline: none;
    width: 168px;
    padding: 8px 0;
    padding-left: 32px;
    border: 1px solid #39394A;
    box-sizing: border-box;
    border-radius: 3px;
    background: #1F1F29;

}
.title__input::placeholder {
    color: #595D6E;
}
.input__wrapper {
    position: relative;
}
.title__search {
    position: absolute;
    top: 9px;
    left: 8px;
}

.match__container {
    padding: 16px 16px 32px 16px;
    width: calc(100% - 32px);
}
.match__one, .match__options {
    width: 100%;
    height: 80px;
    /*background: #39394A;*/
    position: relative;
    background: #2f2f3f;
    border-radius: 5px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: #FFFFFF;
    transition: .5s background;
}
.match__one {
    /*cursor: pointer;*/
}
.match__result div {
    height: 40px;
}
.logo__team img {
    width: 40px;
    height: 43px;
}
.logo__team {
    width: 56px;
    height: 56px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 12px;
    margin-left: 16px;
}
.team_name {
    width: 200px;
    display: flex;
}
.match__options {
    height: 66px;
    border-radius: 0px;
    border-top: 1px solid #48485C;
    background: #39394a;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    color: #FFFFFF;
}
.match__team {
    top: 0;
    left: 0;
    width: 160px;
    background: linear-gradient(270deg, rgba(47, 47, 63, 0.5) 0%, #4E426E 135.99%);
    height: 80px;
    position: absolute;
    transition: .5s opacity;
}
.match__img {
    opacity: 0.5;
    height: 100%;
    transition: .5s all;
}
.match__block__team {
    width: 220px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.block__coef, .block__winLose {
    background: #48485C;
    box-shadow: 0px 4px 16px rgba(34, 34, 45, 0.3), 0px 1px 4px rgba(34, 34, 45, 0.5);
    transition: .5s all;
    border-radius: 4px;
    position: relative;
    /*padding: 9px 28px 7px 29px;*/
    width: 88px;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-style: normal;
    /*font-weight: bold;*/
    font-size: 12px;
    line-height: 14px;
    color: #FFF;
}
.block__winLose {
    background: none;
    box-shadow: none;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
    text-align: right;
    color: #399241;
    text-transform: uppercase;
}
.option_win_line {
    position: absolute;
    background: #399241;
    height: 100%;
    width: 4px;
    top: 0;
}
.coef__arrow {
    position: absolute;
    top: 6px;
    transition: .5s opacity;
    /*z-index: 10;*/
    /*background: black;*/
}
.match__result {
    width: 212px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}
.mini_game_logo {
    position: absolute;
    top: 27px;
    left: 24px;
}
.bottom_open {
    position: absolute;
    bottom: 0;
    width: 134px;
    height: 20px;
    cursor: pointer;
}
.bottom_arrow {
    position: absolute;
    bottom: 7px;
    transition: .4s all;
    left: 50%;
    z-index: 10;
    cursor: pointer;
    transform: translateX(-50%);
}
.options_container {
    
}
.options_wrapper {
    height: 0;
    overflow: hidden;
    transition: .5s height, .5s display;
}

/*.options_wrapper img {*/
/*    width: 100%;*/
/*}*/

.option_button {
    border: 1px solid #48485C;
    box-sizing: border-box;
    border-radius: 3px;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 120%;
    width: 42px;
    height: 54px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 5px;
    cursor: pointer;
    transition: .3s all;
}
.option__title {
    position: absolute;
    top: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 58px;
    width: 58px;
    overflow: hidden;
    background: radial-gradient(50% 50% at 50% 50%, rgba(94, 152, 217, 0.2) 0%, rgba(94, 152, 217, 0) 100%);
    border-radius: 4px;
}
.option__title img {
    margin-top: 15px;
}
.option__list__wrapper {
    background: #2F2F3F;
    height: 70px;
    border-radius: 4px;
    box-shadow: 0px 4px 16px rgba(37, 38, 50, 0.8);
    display: flex;
    position: absolute;
    bottom: 70px;
    transition: .3s opacity;
    box-sizing: border-box;
    z-index: 10000;
}
.option__list__wrapper img {
    margin-top: 8px;
    height: 78px;
    z-index: 10000;
}
