.option_button:hover {
    background: linear-gradient(0deg, rgba(140, 144, 164, 0.1), rgba(140, 144, 164, 0.1)), #47475B;
    border-radius: 3px;
    transition: .3s all;
}
.div_watch, .watch {
    transition: .3s all;
    position: absolute;
    top: 26px;
    right: 26px;
    cursor: pointer;
    /*overflow: hidden;*/
}
.div_watch {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    top: 15px;
    right: 10px;

}
.watch_mes {
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    color: #FFF;
    border-radius: 3px;
    padding: 8px 12px;
    position: absolute;
    top: 26px;
    right: 65px;
    transition: .5s opacity;
}
.watch_mes:after {
    content: "";
    top: 8px;
    position: absolute;
    right: -10px;
    width: 10px;
    height: 13px;
    display: block;
    z-index: 100;
    background: url("img/polygon.png");
    background-repeat: no-repeat;
}
.language__switch {
    width: 100%;
    height: 51px;
    display: flex;
    text-transform: uppercase;
    border-bottom: 2px solid #1F1F29;
    background: #1F1F29;
}
.language__switch span {
    flex : 1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;
    color: #595D6E;
    cursor: pointer;
    transition: .3s all;
}
.completed_date {
    display: flex;
    flex-direction: column;
    position: absolute;
    color: #71768B;
    top: 22px;
    right: 24px;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    width: 53px;
    align-items: flex-start;
    text-align: center;
    justify-content: center;
}
